import logo from './logo.svg';
import './App.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faWhatsapp} from "@fortawesome/free-brands-svg-icons"
import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import {useState} from "react"

const App = () => {
let[city,setCity] = useState('');
let[wDetails,setWdetails] = useState()
let[isLoading,setisLoading] = useState(false)

function getData(e){
  setisLoading(true)
fetch(`https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=751d66e130befad396405dc13796a57c&units=metric`).then((res) => res.json())
.then((finalRes) => {
if(finalRes.cod == "404"){
setWdetails(undefined)
}
else{
setWdetails(finalRes)
}
setisLoading(false)
})
  
e.preventDefault()
setCity('') 
}

  return (
    <div className="container text-center mt-5">
      <h2 className="fw-bold">Simple Weather App</h2>

      <div className="d-flex justify-content-center mt-3">
<form onSubmit = {getData}>       
<div className="d-flex align-items-center">
  <input 
    type="text" 
    value={city} 
    onChange={(e) => setCity(e.target.value)} 
    className="form-control w-50 me-2" 
    placeholder="City Name" 
  />
  <button className="btn btn-dark">Submit</button>
</div>

</form>      
</div>

      <div className="card mt-4 p-4 shadow-lg mx-auto" style={{ maxWidth: "400px" }}>
     
    <img src="https://media2.giphy.com/media/v1.Y2lkPTc5MGI3NjExbXMwN2lzcjdneXNqZWNlNXJscm9teTZoZHN4bzV4bjVwemhsNDBqdiZlcD12MV9pbnRlcm5hbF9naWZfYnlfaWQmY3Q9Zw/3oEjI6SIIHBdRxXI40/giphy.gif" height="170" width="170" 
    className={`d-flex mx-auto  ${isLoading ? '' : "d-none"}`} />


      {
      wDetails!==undefined ? 
      <>
        <h4>
          {wDetails.name} <span className="badge bg-warning text-dark">{wDetails.sys.country}</span>
        </h4>
        <h2> {wDetails.main.temp} </h2>
        <img 
  src={`https://openweathermap.org/img/wn/${wDetails.weather[0].icon}.png`} 
  height="80" 
  width="80" 
  className="d-flex mx-auto"
/>

        <p className="lead">{wDetails.weather[0].description}</p>
        </>
        :"No Data"
      }
      </div>
    </div>
  );
};

export default App;  